import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material';

// openDialogWithAResult
@Component({
    selector: 'dialog-result-example-dialog',
    template: `<h1 mat-dialog-title>Mensaje</h1>
    <div mat-dialog-content style="padding-bottom: 20px;">Desea confirmar?</div>
    <div mat-dialog-actions>
      <button mat-button color="primary" (click)="dialogRef.close(1)">Si</button>
      <button mat-button color="dark"  (click)="dialogRef.close(0)">No</button>
    </div>`,
})
export class EEDialogResultComponent {
    constructor(public dialogRef: MatDialogRef<EEDialogResultComponent>) { }
}



